.header {
  position: relative;
  &:after {
    position: absolute;
    content: "";
    width: 200%;
    height: 1px;
    background-color: rgba($color: #000000, $alpha: 0.2);
    transform: scale(0.5);
    bottom: 0;
    left: -50%;
  }
  .left-logo {
    .logo {
      width: 200px;
    }
  }
}
